@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One|Lato:700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  /* --bg-color: brown; */
  /* --bg-color-highlight: brown; */
  /* --font-color: brown; */
  /* --font-color-highlight: brown; */
  /* --main-bg-color: brown; */
  --bg-color: #dbdfe3;
  --bg-color-secondary: #eaf0f1;
  --text-font: "Montserrat";
  --highlight: #ff8800; /* goldenrod */
  --button-color: #f2545b; /* orange-pink */
  --warning: #a93f55; /* mauve */
  --border: rgba(63, 136, 197, 0.2); /* light blue */
  --font-color: #022f40; /* dark blue */
  /* --button-color: var(--empahsize); */
}

body,
input {
  font-family: "Montserrat", Verdana, Geneva, Tahoma, sans-serif;
  font-family: var(--text-font), Verdana, Geneva, Tahoma, sans-serif;
  color: #022f40;
  color: var(--font-color);
  font-size: 14px;
  /* background-color: var(--bg-color); */
}

/* Material Design Icon Rules */
@font-face {
  font-family: "Material Icons";
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.googleapis.com/icon?family=Material+Icons);
}

.material-icons.md-24 {
  font-size: 24px;
}
.material-icons.md-dark {
  color: rgba(0, 0, 0, 0.54);
}
.material-icons.md-dark.md-inactive {
  color: rgba(0, 0, 0, 0.26);
}
.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px; /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  -webkit-font-feature-settings: "liga";
          font-feature-settings: "liga";
}

.app-container {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px auto 50px;
  grid-template-areas:
    "header"
    "body"
    "footer";
}

.app-body {
  /* height: calc(100vh - 100px); */
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "drawer-left content drawer-right";
}

.app-footer {
  /* bottom: 0px; */
  background-color: #eaf0f1;
  background-color: var(--bg-color-secondary);
  grid-area: footer;
  /* width: 100vw; */
  /* height: 50px; */
}

.drawer-left,
.drawer-right {
  background-color: #eaf0f1;
  background-color: var(--bg-color-secondary);
}
.drawer-left {
  grid-area: drawer-left;
}

.drawer-right {
  grid-area: drawer-right;
}

.content-container {
  margin: 2px;
  display: grid;
  grid-template-rows: 300px auto;
  grid-template-columns: 1fr;
  grid-template-areas:
    "header"
    "body";
  box-shadow: 2px 2px 20px 20px #eaf0f1;
  box-shadow: 2px 2px 20px 20px var(--bg-color-secondary);
  border-radius: 10px;
}

.content-header {
  grid-area: header;
  /* background-color: lightgray; */
  display: grid;
  grid-template-columns: 300px auto;
  grid-template-rows: -webkit-min-content;
  grid-template-rows: min-content;
  grid-auto-rows: -webkit-min-content;
  grid-auto-rows: min-content;
  grid-template-areas: "doughnut recipe-header";

  margin: 2px 5px;
  border-top: none;
  border-bottom: 2px solid #ff8800;
  border-bottom: 2px solid var(--highlight);
  box-shadow: 2px 2px 10px 5px #eaf0f1;
  box-shadow: 2px 2px 10px 5px var(--bg-color-secondary);
  border-radius: 10px 10px 0 0;
}

.content-body {
  grid-area: body;
  /* background-color: white; */
  overflow: scroll;
}

.recipe-form {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.recipe-graph-doughnut-container {
  grid-area: doughnut;
  height: 300px;
  width: 300px;
}

.recipe-form footer {
  position: relative;
  height: 50px;
}

.recipe-step-card {
  border-top: none;
  border-bottom: 2px solid #ff8800;
  border-bottom: 2px solid var(--highlight);
  box-shadow: 2px 2px 10px 5px #eaf0f1;
  box-shadow: 2px 2px 10px 5px var(--bg-color-secondary);
  border-radius: 10px 10px 0 0;

  margin: 2px 5px;
  padding: 5px;
}

.step-name-container {
  width: 100%;
  height: auto;
  min-height: 100px;
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-auto-rows: 40px auto 40px;
  grid-template-areas:
    "step-name ratio-bar"
    "ingredients ingredients"
    "add-ingredient-recipe yield-and-edit";
}

.step-properties {
  display: grid;
  grid-template-columns: 1fr 40px;
  grid-template-areas: "step-name color-swatch";
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.step-yield-container {
  grid-area: yield-and-edit;
  display: grid;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  grid-template-columns: 3fr 1fr 1fr 3fr 0.5fr 0.5fr;
  grid-template-areas: "label yield unit . save delete";
}

.step-yield-container > * {
  vertical-align: -10px;
}

.step-yield-container > label {
  grid-area: label;
}

.step-yield-quantity {
  grid-area: yield;
  width: 50px;
}

.step-yield-quantity-label {
  padding: 10px;
  text-align: right;
}

.step-yield-unit {
  grid-area: unit;
  text-align: left;
}

.step-submit {
  grid-area: save;
}
.step-delete {
  grid-area: delete;
}

.step-color {
  grid-area: color-swatch;
}

.step-name {
  grid-area: step-name;
  width: 100%;
  line-height: 30px;
}

.step-ratio-bar {
  grid-area: ratio-bar;
  display: flex;
  flex-wrap: nowrap;
  padding: 2px;
}

.step-ratio-element {
  border-radius: 5px;
  margin: 2px;
}

.step-ratio-element:hover {
  box-shadow: -5px -5px 10px rgba(0, 0, 0, 0.1), 5px 5px 10px rgba(0, 0, 0, 0.1);
}

.step-add-button {
  position: absolute;
  right: 10px;
  top: 10px;
}

.recipe-ingredients {
  grid-area: ingredients;
}

.ingredient-card {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-auto-rows: 1fr;
  grid-template-areas: "ratio ingredient";
  border-bottom: 1px dotted rgba(63, 136, 197, 0.2);
  border-bottom: 1px dotted var(--border);
  margin-bottom: 1px;
  /* box-shadow: 0 -10px -10px 10px var(--border); */
}

.ingredient-form {
  background-color: #eaf0f1;
  background-color: var(--bg-color-secondary);
}

.ingredient-ratio-container {
  grid-area: ratio;
  text-align: right;
  display: flex;
  flex-direction: row-reverse;
}

.ingredient-ratio {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  margin: 2px;
  height: 25px;
}

.ingredient-properties {
  grid-area: ingredient;
  /* background-color: #a7a7a7; */
  display: grid;
  grid-template-columns: 3fr 1fr 1fr 3fr 0.5fr 0.5fr;
  grid-template-areas: "ingredient quantity unit action submit delete";
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.ingredient-color-swatch,
.step-color-swatch {
  height: 35px;
  width: 35px;
  margin: 2px;
  border-radius: 5px;
  position: relative;
}

.ingredient-color-picker,
.step-color-picker {
  position: absolute;
  top: 13px;
  left: -5px;
  z-index: 4;
}

.ingredient-name,
.ingredient-quantity,
.ingredient-unit,
.ingredient-instruction {
  line-height: 30px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.ingredient-name {
  grid-area: ingredient;
}

.ingredient-quantity {
  grid-area: quantity;
  text-align: right;
}
.ingredient-unit {
  grid-area: unit;
  text-align: left;
}
.ingredient-instruction {
  grid-column: action;
  text-align: left;
}

input {
  background-color: rgba(0, 0, 0, 0);
  /* border: none; */
  border-top: none;
  border-right: none;
  border-bottom: 2px solid rgba(63, 136, 197, 0.2);
  border-bottom: 2px solid var(--border);
  border-left: none;
  /* font-size: 16px; */
}
input:focus,
input:hover {
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  border-bottom-color: #ff8800;
  border-bottom-color: var(--highlight);
}

input.ingredient-name,
input.ingredient-quantity,
input.ingredient-unit,
input.ingredient-instruction {
  width: 100%;
}

.ingredient-submit,
.ingredient-delete {
  border: none;
  border-radius: 5px;
}
.ingredient-submit {
  grid-area: submit;
}

.ingredient-delete {
  grid-area: delete;
}

@media screen and (max-width: 900px) {
  body,
  input {
    font-size: 12px;
  }

  .app-body {
    grid-template-columns: 1fr;
    grid-template-areas: "content";
  }
  .drawer-left,
  .drawer-right {
    display: none;
  }

  .content-container {
    width: 100vw;
    grid-template-rows: auto;
  }

  .content-body {
    margin-left: auto;
    margin-right: auto;
  }

  .content-header {
    margin: 5px;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 300px;
    grid-template-areas: "recipe-header" "doughnut";
  }

  .recipe-graph-doughnut-container {
    margin-left: auto;
    margin-right: auto;
    height: 300px;
  }

  .ingredient-properties {
    grid-template-columns: 3fr 1fr 1fr 3fr 40px 40px;
    grid-gap: 0.2rem;
    gap: 0.2rem;
  }

  .ingredient-card {
    grid-template-columns: 1fr 6fr;
  }
}

.onload-heroku-notice {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: var(--bg-color);
  opacity: 1;
  font-size: 20px;
  color: var(--highlight);
  font-weight: bold;
  text-shadow: -0.5px -0.5px 0px white, 3px 2px 3px rgba(0,0,0,0.2);
  text-align: center;
}

.heroku-notice-container {
  position: relative;
  width: 300px;
  height: 200px;
  top: calc(45vh - 100px);
  left: calc(50vw - 150px);
}


.onload-heroku-notice.hide {
  -webkit-animation: fadeToNone 0.6s ease-in-out;
          animation: fadeToNone 0.6s ease-in-out;
  opacity: 0;
}

.heroku-notice-container.hide {
  -webkit-animation: fadeToNone 0.8s ease-in-out;
          animation: fadeToNone 0.8s ease-in-out;
  opacity: 0;
}

.hidden {
  display: none;
}

@-webkit-keyframes fadeToNone {
  0% {
    display: block;
    opacity: 1;
  }
  99% {
    display: block;
    opacity: 0;
  }
  100% {
    display: none;
    opacity: 0;
  }
}

@keyframes fadeToNone {
  0% {
    display: block;
    opacity: 1;
  }
  99% {
    display: block;
    opacity: 0;
  }
  100% {
    display: none;
    opacity: 0;
  }
}
button {
  background-color: rgba(0, 0, 0, 0);
}

.button {
  border: none;
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.button-container {
  position: relative;
  width: 100%;
}

.button-block {
  display: inline-block;
  text-align: center;
  background-color: var(--button-color);
  border-radius: 5px;
  border: none;
  height: 35px;
  width: 35px;
  color: white;
}

.button span {
  font-size: 16px;
}

@media screen and (max-width: 900px) {
  .button span {
    font-size: 12px;
  }
}

.button:hover {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.button-icon {
  position: relative;
  top: 5px;
}

.button-tip {
  position: fixed;
  top: 0px;
  background-color: var(--bg-color);
  display: block;
  text-align: left;
  z-index: 1000;
  width: 100px;
}

.button-tip-hide {
  display: none;
}

.menu.button {
  width: calc(100% - 10px);
  max-width: 200px;
  margin: 5px;
  align-self: center;
}

.menu.button-block {
  width: 100%;
  padding: 2px 5px;
  text-align: left;
}

.menu.button-icon {
  margin: 0px 5px;
}

.app-header {
  background-color: var(--bg-color-secondary);
  grid-area: header;
  position: relative;
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-template-areas: "logo nav";
}

.app-header nav {
  grid-area: nav;
  display: flex;
  flex-direction: row-reverse;
  /* align-content: center; */
  /* justify-content: space-between; */
  /* width: 10%; */
}

.hamburger-container {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: rgba(255, 255, 255, .1);
}

.burger, .header-menu-burger {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
}

.header-menu-burger {
  width: 200px;
  height: 250px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  background-color: white;
  border-top: none;
  box-shadow: 2px 2px 10px 5px var(--bg-color);
  border-radius: 0 0 0 10px;
  z-index: 10;
}


.Hamburger {
  height: 30px;
  width: 30px;
}

.app-logo {
  position: absolute;
  top: -35px;
  left: 10px;
  align-self: start;
  font-size: 45px;
  color: var(--highlight);
  font-weight: bold;
  text-shadow: -0.5px -0.5px 0px white, 3px 2px 3px rgba(0,0,0,0.2);
}
.drawer-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-around;
  /* align-items: center; */

  flex-wrap: nowrap;
  margin: 20px 0px;
  width: calc(100% - 10px);
}

.button-nav {
  width: 80%;
  height: 70px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 1200px) {
  .button-nav {
    width: 100%;
  }
}

@media screen and (max-width: 900px) {
  .button-nav {
    height: 50px;
  }
}

section.auth {
  width: 50%;
  min-height: 200px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  border-top: none;
  border-bottom: 2px solid var(--highlight);
  box-shadow: 2px 2px 10px 5px var(--bg-color-secondary);
  border-radius: 10px 10px 0 0;
  display: grid;
  grid-template-rows: 50px auto;
  grid-template-areas: "header" "form";
}

section.auth.sign-in {
  height: 200px;
}

section.auth.sign-up {
  height: 300px;
}


header.auth {
  grid-area: header;
  background-color: rgba(49, 95, 133, 0.2);
  border-radius: 10px 10px 0 0;
  height: 50px;
}

header.auth h1 {
  color: white;
  width: 100%;
  text-align: center;
  text-shadow: -0.5px -0.5px 1px white, 0.4px 0.4px 3px rgba(0, 0, 0, 0.3);
  color: var(--highlight);
  font-weight: bold;
  height: 30px;
  vertical-align: middle;
  margin-bottom: 15px;
  line-height: 10px;
}

form.auth {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.label-input-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-areas: "label input";
  grid-gap: 5px;
  gap: 5px;
}

.label-input-container label {
  grid-area: label;
  text-align: right;
  padding: 10px 0 10px 0;
  font-weight: bold;
  color: gray;
}

.label-input-container input {
  grid-area: input;
  width: 100%;
  margin: none;
  padding: 10px 0 10px 0;
}

form.auth .submit,
form.auth .alternate {
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: 150px;
}

.auth.buttons {
  grid-area: buttons;
  /* font-family: var(--text-font); */
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "submit alternate";
}

.auth.submit {
  grid-area: submit;
}
.auth.alternate {
  grid-area: alternate;
}

@media screen and (max-width: 900px) {
  section.auth {
    width: 90%;
    height: 300px;
  }

  form.auth .submit,
  form.auth .alternate {
    padding-top: 10px;
    padding-bottom: 10px;
    max-width: 110px;
  }

  header.auth h1 {
    line-height: 20px;
  }
}

.recipe-gallery {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
}

.recipe-gallery-card {
  width: 300px;
  height: 300px;
  display: grid;
  grid-template-rows: 3fr 1fr 2fr;
  grid-template-areas:
    "image"
    "title"
    "description";
  border-top: none;
  border-bottom: 3px solid var(--border);
  box-shadow: 2px 2px 10px 5px var(--bg-color-secondary);
  border-radius: 10px 10px 0 0;

  margin: 10px;
  padding: 5px;
}

.recipe-gallery-card-container {
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.recipe-gallery-card-container:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.recipe-gallery-card:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.recipe-gallery-button-container {
  position: absolute;
}

.recipe-gallery-button-relative-container {
  position: relative;
  top: 20px;
  z-index: 4;
  width: 100%;
}

.recipe-share,
.recipe-favorite {
  position: relative;
  right: -15px;
}

.recipe-remove {
  position: relative;
  right: -210px;
}

.icon-button .button-block {
  border: 2px solid white;
}

.recipe-image {
  border-radius: 10px 10px 0 0;
  grid-area: image;
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.recipe-title {
  grid-area: title;
}
.recipe-description {
  grid-area: description;
}

.recipe-header {
  grid-area: recipe-header;
}

.recipe-header-display,
.recipe-header-form {
  width: 100%;
  height: 100%;
  display: grid;
  margin-bottom: 10px;
  grid-template-rows: 50px auto 50px 50px;
  grid-template-columns: 1fr 50px;
  grid-template-areas: 
    "title title"
    "description description"
    "image-url image-url"
    ". button";
}

/* Edit button for recipe header form */
.recipe-header-edit,
.recipe-submit {
