.unit-select {
  width: 100%;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 0px;
  border-top: none;
  border-right: none;
  border-bottom: 2px solid var(--border);
  border-left: none;
}
.app-footer {
  width: 100%;
  text-align: center;
  padding-top: 15px;
}
.home-page {
  width: 100%;
  height: calc(100vh - 100px);
  display: grid;
  grid-template-rows: calc(100vw / 4) 1fr 50px;
  grid-template-areas:
    "image"
    "body"
    "footer";
}

.home-header {
  grid-area: image;
  width: 100%;
  height: 100%;
}

img.splash-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.home-description {
  grid-area: body;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grit-template-rows: auto 50px;
  grid-template-areas: 
    ". descriptions ."
    ". register .";
}

.description-points {
  grid-area: descriptions;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  list-style: none;
  padding-left: 0px;
  grid-gap: 30px;
  gap: 30px;
  
}

.description-point {
  width: calc(100vw / 4);
  max-width: 300px;
  height: 100px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  align-self: top;
  
}

.description-point-title {
  color: black;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  
}

.get-started {
  grid-area: register;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 900px) {
  .home-description {
    grid-template-columns: 20px 2fr 20px;
  }

  .description-points {
    grid-template-columns: 1fr;
  }

  .description-point {
    width: 100%;
  }
}

.window-modal {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.1);
  z-index: 100;
}

.error-modal {
  position: relative;
  width: 300px;
  /* min-height: 200px; */
  max-height: 200px;
  background-color: var(--highlight);
  z-index: 5;
  left: calc(50vw - 150px);
  top: calc(25vw - 100px);
  border-radius: 10px;
  box-shadow: 2px 2px 10px 5px var(--bg-color-secondary) -2px -2px 10px 5px
    var(--bg-color-secondary);
  /* margin-left: auto; */
  /* margin-right: auto; */
  display: grid;
  grid-template-rows: 40px 1fr 30px;
  grid-template-areas: "header" "body" "footer";
  padding: 20px;
}

.error-modal-title {
  grid-area: header;
  text-align: center;
  color: white;
}

.error-modal-message,
.error-modal-close {
  /* padding: 20px 10px; */
  text-align: center;
  color: white;
}

.error-modal-message {
  grid-area: body;
}

.error-modal-close {
  grid-area: footer;
}

